import React, { Component } from "react";
import { createNumberMask } from "redux-form-input-masks";
import { Field, formValueSelector, reduxForm } from "redux-form";
import { connect } from "react-redux";
import { renderField } from "../../renderField";
import { warning } from "../../Alert";

const currencyMask = createNumberMask({
  prefix: "Rp. ",
  suffix: ",-",
  locale: "id-ID",
});


class AccordTitipMurni extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  componentDidMount() {
    var titip = 0,
      harga = 0,
      netto = 0;
    var local = JSON.parse(localStorage.getItem("alatBayarTitipRp"))
      ? JSON.parse(localStorage.getItem("alatBayarTitipRp"))
      : "kosong";
    if (local === "kosong") {
      titip = 0;
      harga = 0;
      netto = 0;
    } else {
      titip = local[0].titip_rp;
      harga = local[0].harg_emas;
      netto = local[0].titip_netto;
    }
    this.props.change("titip", titip);
    this.props.change("harga", harga);
    this.props.change("titipNetto", netto);
  }

  onChange() {
    if(this.props.saldo_titip_rp < this.props.titipNetto ){
      warning("Pembayaran Melebihi Titipan Tersedia");
      return false
    }
    this.props.change("titipNetto", this.props.titipNetto);
  }
  render() {
    return (
      <div className="col-lg-6 mt-3">
        <div id="accordion" className="custom-accordion mb-4">
          <div className="card mb-0 bg-dark">
            <div className="card-header " id="headingOne">
              <div className="row">
                <div className="col-lg-8">
                  <h5 className="m-0">
                    <a
                      className="custom-accordion-title d-block pt-2 pb-2 text-white"
                      data-toggle="collapse"
                      href="#collapseTitip"
                      aria-expanded="true"
                      aria-controls="collapseTitip"
                    >
                      Titip
                    </a>
                  </h5>
                </div>
                <div className="col-md-4 col-lg-4">
                  <label htmlFor="" style={{ color: "white" }}>
                    Netto
                  </label>
                  <Field
                    name="titipNetto"
                    type="telp"
                    component="input"
                    className="form-control"
                    readOnly={true}
                    {...currencyMask}
                  />
                </div>
              </div>
            </div>
            <div
              id="collapseTitip"
              className="collapse"
              aria-labelledby="headingOne"
              data-parent="#accordion"
            >
              <div className="card-body bg-white">
                <form onSubmit={this.props.handleSubmit}>
                  <div className="row">
                  <div className="col-lg-12 mb-2 justify-content-center">
                      <h4>Saldo Titip Tersedia :<strong> {this.props.saldo_titip_rp}</strong></h4>
                    </div>
                    <div className="col-md-4 col-lg-4">
                      <label htmlFor="">Jumlah Putus</label>
                      <Field
                        name="titip"
                        component="input"
                        type="tel"
                        className="form-control"
                        onBlur={this.onChange()}
                        {...currencyMask}
                      />
                    </div>
                    <div className="col-md-4 col-lg-4">
                      <label htmlFor="">Harga </label>
                      <div className="form-gorup">
                        <Field
                          name="harga"
                          component="input"
                          type="tel"
                          readOnly
                          className="form-control"
                          onBlur={this.onChange()}
                          {...currencyMask}
                        />
                      </div>
                    </div>
                    <div className="col-md-4 col-lg-4">
                      <Field
                        name="titipNetto"
                        label="Netto"
                        type="telp"
                        component={renderField}
                        readOnly={true}
                        {...currencyMask}
                      />
                    </div>
                    <div className="col-lg-12">
                      <div className="text-right">
                        <button type="submit" className="btn btn-primary">
                          Simpan
                        </button>
                      </div>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

AccordTitipMurni = reduxForm({
  form: "AccordTitipMurni",
  enableReinitialize: true,
})(AccordTitipMurni);
const selector = formValueSelector("AccordTitipMurni"); // <-- same as form name
export default connect((state) => {
  const {  titip } = selector(state, "harga", "titip");
  return {
    titipNetto: parseFloat(titip) || 0 ,
    titipNettoMurni: parseFloat(titip) || 0,
    saldo_titip_rp: state.pembayaranCustomer.titip_rp,
    saldo_titip_gr: state.pembayaranCustomer.titip_gr,
  };
})(AccordTitipMurni);
